export default {
	data() {
	    return {
			shopAddress:{},
			shopObj:{},
			def:1,
		}
	},
	methods: {
		radioChange(e){
			//选中的值
			console.log(e.detail.value)
		},
		edit(e){
			uni.navigateTo({
				url:"/pages/system/addAddress"
			})
		},
		del(e){
			console.log(e)
		},
		url(e){
			uni.navigateTo({
				url:e.currentTarget.dataset.url
			})
		}
	},
	onLoad(){
		let that=this;
		uni.getStorage({
			key:"shopObj",
			success(res){
				that.shopObj=JSON.parse(res.data);
			}
		})
	},
	onShow(){
		let that=this;
		that.$ajax({
			url:"/merchant/returnAddress",
			data:{
				merchantId:that.shopObj.merchantId,
			},success(d){
				that.shopAddress=d
			}
		})
	}
}
